import { Component, OnInit, DoCheck } from '@angular/core';
import {ProfTitle} from '../entity';
import {UniversityService} from '../university.service';

@Component({
  selector: 'app-prof-title',
  templateUrl: './prof-title.component.html',
  styleUrls: ['./prof-title.component.css']
})
export class ProfTitleComponent implements OnInit, DoCheck{

  profTitles: ProfTitle[];
  private dataShow: ProfTitle[]; // 分页后在当前页要显示的数据
  public leng: number;                 // 数据总量，需要传给子组件的变量
  public currentPag: number;           // 保存从子组件中传来的当前显示页面
  public pagNums: number;              // 保存从子组件中传来的每页显示数量
  public oldCurrentPag: number;
  public oldPagNums: number;
  constructor(private profTitleService: UniversityService) { }

  ngOnInit() {
    this.getprofTitles();
  }
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      const curren = this.currentPag - 1;
      const pagNum = this.pagNums;
      let startNum = curren * pagNum;
      for ( let i = 0; i < pagNum; i++, startNum++) {
        if ( this.profTitles[startNum] !== null && this.profTitles[startNum] !== undefined) {
          this.dataShow.push(this.profTitles[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 捕获当前显示页码函数
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }
  // 捕获每页显示数量行数
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getprofTitles(): void {
    this.profTitleService.getProfTitles()
      .subscribe(res => {this.profTitles = res; this.leng = res.length; });
  }
  delete(profTitle: ProfTitle | number): void {
    this.profTitles = this.profTitles.filter(h => h !== profTitle);
    this.profTitleService.deleteProfTitle(profTitle).subscribe(res => alert(res.message));
  }
}
