import { Component, OnInit, DoCheck} from '@angular/core';
import {Degree} from '../entity';
import {UniversityService} from '../university.service';

@Component({
  selector: 'app-degree',
  templateUrl: './degree.component.html',
  styleUrls: ['./degree.component.css']
})
export class DegreeComponent implements OnInit, DoCheck {
  degrees: Degree[];
  private dataShow: Degree[]; // 分页后在当前页要显示的数据
  public leng: number;                 // 数据总量，需要传给子组件的变量
  public currentPag: number;           // 保存从子组件中传来的当前显示页面
  public pagNums: number;              // 保存从子组件中传来的每页显示数量
  public oldCurrentPag: number;
  public oldPagNums: number;
  constructor(private degreeService: UniversityService) { }

  ngOnInit() {
    this.getdegrees();
  }
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      const curren = this.currentPag - 1;
      const pagNum = this.pagNums;
      let startNum = curren * pagNum;
      for ( let i = 0; i < pagNum; i++, startNum++) {
        if ( this.degrees[startNum] !== null && this.degrees[startNum] !== undefined) {
          this.dataShow.push(this.degrees[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }
// 捕获当前显示页码函数
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }
  // 捕获每页显示数量行数
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }
  getdegrees(): void {
    this.degreeService.getDegrees()
      .subscribe(res => {this.degrees = res; this.leng = res.length; });
  }
  delete(degree: Degree | number): void {
    this.degrees = this.degrees.filter(h => h !== degree);
    this.degreeService.deleteDegree(degree).subscribe(res => alert(res.message));
  }

}
