import {Component, Input, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {UniversityService} from '../university.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-add-prof-title',
  templateUrl: './add-prof-title.component.html',
  styleUrls: ['./add-prof-title.component.css']
})
export class AddProfTitleComponent implements OnInit {

  @Input() profTitle: { no: string; description: string; remarks: string };
  constructor(
    private route: ActivatedRoute,
    private profTitleService: UniversityService,
    private location: Location
  ) { }

  ngOnInit() {
  }
  save(description: string, no: string, remarks: string): void {
    this.profTitle = {no: no.trim(), description: description.trim(), remarks: remarks.trim()};
    if (!description) { return; }
    this.profTitleService.addProfTitle(this.profTitle)
      .subscribe(
        (res) => {
          alert(res.message);
          this.goBack();
        }
      );
  }

  goBack(): void {
    this.location.back();
  }

}
