import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { UniversityService} from '../university.service';
import { Teacher, Degree, ProfTitle, College,  Department } from '../entity';

@Component({
  selector: 'app-add-teacher',
  templateUrl: './add-teacher.component.html',
})
export class AddTeacherComponent implements OnInit {
  @Input() schools: College[];
  degrees: Degree[];
  titles: ProfTitle[];
  departments: Department[];
  teacher: Teacher;
  school: College;
  degree: Degree;
  title: ProfTitle;
  department: Department;
  constructor(
    private route: ActivatedRoute,
    private teacherService: UniversityService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getColleges() ;
    this.getDegrees();
    this.getTitles();
    this.getDepartments();
  }
  getColleges(): void {
    this.teacherService.getColleges ()
      .subscribe(res => this.schools = res); // 将返回到的参数赋值给schools
  }
  getDegrees(): void {
    this.teacherService.getDegrees ()
      .subscribe(res => this.degrees = res);
  }
  getTitles(): void {
    this.teacherService.getProfTitles()
      .subscribe(res => this.titles = res);
  }
  getDepartments(): void {
    this.teacherService.getDepartments ()
      .subscribe(res => this.departments = res);
  }
  getDepSch(id: number): void {
    this.teacherService.getDepartment(id)
      .subscribe(res => this.department = res);
  }
  save(name: string, no: string, title: ProfTitle, degree: Degree, department: Department): void {
    // 通过传参传入teacher对象的字段值
    this.teacher = {
      id: null,
      name: name.trim(), // 去掉字符序列左右两边的空格
      no: no.trim(),
      title: this.title,
      degree: this.degree,
      department: this.department
    };
    if (!name) { return; }
    // 将teacher对象存入数据库中
    this.teacherService.addTeacher(this.teacher)
      .subscribe(
        (res) => {
          alert(res.message);
          this.goBack();
        }
      ); // 调用匿名函数跳转历史页面
  }

  goBack(): void {
    this.location.back();
  }
}
