import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {UniversityService} from '../university.service';
import {College,  Department1} from '../entity';
@Component({
  selector: 'app-add-dep',
  templateUrl: './add-dep.component.html'
})
export class AddDepComponent implements OnInit {
  @Input() colleges: College[];
  department: Department1;
  school: College;
    constructor(
    private route: ActivatedRoute,
    private departmentService: UniversityService,
    private location: Location
  ) { }

  ngOnInit() {
      this.getColleges() ;
    }
  getColleges(): void {
    this.departmentService.getColleges ()
      .subscribe(res => this.colleges = res);
  }
  getDepSch(id: number): void {
    this.departmentService.getDepartment(id)
      .subscribe(res => this.department = res);
  }

  save(description: string, no: string, school: College, remarks: string): void { // 多个文本框数据
    this.department = {no: no.trim(), description: description.trim(), school: this.school, remarks: remarks.trim()};
    if (!description) { return; }
    this.departmentService.addDepartment(this.department)
      .subscribe(
        (res) => {
          alert(res.message);
          this.goBack();
        }
      );
  }

  goBack(): void {
    this.location.back();
  }

}
